import { motion } from 'framer-motion';
import { ArrowDown } from 'lucide-react';

const Hero = ({ settings }: { settings?: any }) => {
    const title = settings?.title || "Designing the Digital Future";
    const subtitle = settings?.subtitle || "I am a Full Stack Developer passionate about crafting intuitive, scalable, and visually stunning web applications.";
    const ctaText = settings?.ctaText || "View Projects";

    return (
        <section id="home" className="min-h-screen flex flex-col justify-center items-center px-6 relative overflow-hidden">
            {/* Background Gradient Blob */}
            <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-[800px] h-[800px] bg-accent/5 rounded-full blur-[120px] -z-10 pointer-events-none" />

            <div className="max-w-5xl mx-auto text-center z-10 space-y-8">
                <motion.div
                    initial={{ opacity: 0, y: 30 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.8, ease: "easeOut" }}
                >
                    <span className="inline-block py-1 px-3 rounded-full bg-white/5 border border-white/10 text-xs font-medium tracking-wider text-accent uppercase mb-4">
                        Available for new projects
                    </span>
                    <h1 className="font-display font-bold text-5xl md:text-8xl tracking-tighter text-white mb-6 leading-[1.1]" dangerouslySetInnerHTML={{ __html: title.split('\n').join('<br/>') }}>

                    </h1>
                </motion.div>

                <motion.p
                    className="text-secondary text-lg md:text-xl max-w-2xl mx-auto font-light leading-relaxed"
                    initial={{ opacity: 0, y: 30 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.8, delay: 0.2, ease: "easeOut" }}
                >
                    {subtitle}
                </motion.p>

                <motion.div
                    className="flex flex-col sm:flex-row gap-4 justify-center items-center pt-8"
                    initial={{ opacity: 0, y: 30 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.8, delay: 0.4, ease: "easeOut" }}
                >
                    <a
                        href="#projects"
                        className="px-8 py-3 bg-white text-black font-semibold rounded-full hover:bg-gray-200 transition-all transform hover:scale-105"
                    >
                        {ctaText}
                    </a>
                    <a
                        href="#contact"
                        className="px-8 py-3 border border-white/10 text-white font-medium rounded-full hover:bg-white/5 transition-all"
                    >
                        Contact Me
                    </a>
                </motion.div>
            </div>

            {/* Scroll Indicator */}
            <motion.div
                className="absolute bottom-10 left-1/2 -translate-x-1/2 text-secondary/50 animate-bounce"
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                transition={{ delay: 1, duration: 1 }}
            >
                <ArrowDown size={20} />
            </motion.div>
        </section>
    );
};

export default Hero;
