"use client";

import { useState } from 'react';
import { AnimatePresence } from 'framer-motion';
import Navbar from './Navbar';
import Hero from './Hero';
import About from './About';
import Projects, { ProjectType } from './Projects';
import Contact from './Contact';
import Footer from './Footer';
import Preloader from './Preloader';
import SmoothScroll from './SmoothScroll';

interface ClientHomeProps {
    projects: ProjectType[];
    settings?: any;
    experience?: any[];
}

export default function ClientHome({ projects, settings, experience }: ClientHomeProps) {
    const [loading, setLoading] = useState(true);

    return (
        <>
            <AnimatePresence mode='wait'>
                {loading && <Preloader onComplete={() => setLoading(false)} />}
            </AnimatePresence>

            {!loading && (
                <div className="bg-background min-h-screen text-secondary font-sans selection:bg-accent selection:text-white">
                    <Navbar />
                    <SmoothScroll>
                        <Hero settings={settings?.hero} />
                        <About settings={settings?.about} experience={experience} />
                        <Projects projects={projects} />
                        <Contact settings={settings?.contact} />
                        <Footer settings={settings?.contact} />
                    </SmoothScroll>
                </div>
            )}
        </>
    );
}
