import { motion } from 'framer-motion';
import { ArrowUpRight } from 'lucide-react';
import Image from 'next/image';

export interface ProjectType {
    _id?: string;
    title: string;
    description: string;
    image: string;
    tags: string[];
    link?: string;
}

const Projects = ({ projects }: { projects: ProjectType[] }) => {
    // Fallback if no projects provided (e.g. error fetching)
    const displayProjects = projects.length > 0 ? projects : [
        {
            title: "Coming Soon",
            description: "Projects are being added. Stay tuned.",
            tags: ["WIP"],
            image: "https://images.unsplash.com/photo-1555066931-4365d14bab8c?auto=format&fit=crop&q=80&w=1000",
            link: "#"
        }
    ];

    return (
        <section id="projects" className="py-24 px-6 bg-background">
            <div className="max-w-7xl mx-auto">
                <motion.h2
                    initial={{ opacity: 0, y: 20 }}
                    whileInView={{ opacity: 1, y: 0 }}
                    viewport={{ once: true }}
                    className="font-display text-4xl md:text-5xl font-bold mb-16 text-white text-center md:text-left"
                >
                    Selected <span className="text-accent">Works</span>
                </motion.h2>

                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
                    {displayProjects.map((project, index) => (
                        <div key={index} className="group block relative">
                            <motion.div
                                initial={{ opacity: 0, y: 20 }}
                                whileInView={{ opacity: 1, y: 0 }}
                                transition={{ delay: index * 0.1 }}
                                viewport={{ once: true }}
                            >
                                <a href={project._id ? `/projects/${project._id}` : (project.link || '#')} className="block relative aspect-[4/3] rounded-2xl overflow-hidden mb-6 border border-white/10 group-hover:border-accent/30 transition-colors cursor-pointer">
                                    <div className="absolute inset-0 bg-black/20 group-hover:bg-black/0 transition-colors z-10" />
                                    <img
                                        src={project.image}
                                        alt={project.title}
                                        className="object-cover w-full h-full transform group-hover:scale-105 transition-transform duration-700"
                                    />
                                    <div className="absolute top-4 right-4 z-20 bg-black/50 backdrop-blur-md p-2 rounded-full opacity-0 group-hover:opacity-100 transition-opacity translate-y-2 group-hover:translate-y-0">
                                        <ArrowUpRight className="text-white w-5 h-5" />
                                    </div>
                                </a>

                                <h3 className="text-xl font-bold text-white mb-2 group-hover:text-accent transition-colors">
                                    <a href={project._id ? `/projects/${project._id}` : (project.link || '#')}>{project.title}</a>
                                </h3>
                                <p className="text-secondary text-sm mb-4 line-clamp-2">
                                    {project.description}
                                </p>
                                <div className="flex flex-wrap gap-2">
                                    {project.tags.map(tag => (
                                        <span key={tag} className="text-xs font-medium px-2 py-1 rounded bg-white/5 text-secondary border border-white/5">
                                            {tag}
                                        </span>
                                    ))}
                                </div>
                            </motion.div>
                        </div>
                    ))}
                </div>
            </div>
        </section>
    );
};

export default Projects;
